import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import People from "@material-ui/icons/People";
import Email from "@material-ui/icons/Email";
import Phone from "@material-ui/icons/Phone";
import Message from "@material-ui/icons/Message";
import Map from "@material-ui/icons/map";
// React icons
import { FaTwitter, FaFacebook, FaGooglePlusG } from 'react-icons/fa';
// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Card from "components/Card/Card.jsx";
import CardHeader from "components/Card/CardHeader.jsx";
import CardBody from "components/Card/CardBody.jsx";
import CardFooter from "components/Card/CardFooter.jsx";
import Button from "components/CustomButtons/Button.jsx";
import CustomInput from "components/CustomInput/CustomInput.jsx";

import loginStyle from "assets/jss/material-kit-react/views/componentsSections/loginStyle.jsx";

class SectionLogin extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.section}>
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={12} md={6}>
              <Card>
                <form className={classes.form}>
                  <CardHeader color="primary" className={classes.cardHeader}>
                    <h4>Contact</h4>
                    <div className={classes.socialLine}>
                      <Button
                        justIcon
                        href="https://twitter.com/shri_vitthal"
                        target="_blank"
                        color="transparent"
                        onClick={"e => e.preventDefault()"} // later remove the text in on click
                      >
                        <FaTwitter/>
                      </Button>
                      <Button
                        justIcon
                        href="https://www.facebook.com/hindutempletoronto/"
                        target="_blank"
                        color="transparent"
                        onClick={"e => e.preventDefault()"}
                      >
                        <FaFacebook/>
                      </Button>
                      <Button
                        justIcon
                        href="#"
                        target="_blank"
                        color="transparent"
                        onClick={"e => e.preventDefault()"}
                      >
                        <Map/>
                      </Button>
                    </div>
                  </CardHeader>
                  <p className={classes.divider}>Send Us A Message</p>
                  <CardBody>
                    <CustomInput
                      labelText="First Name*"
                      id="first"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        type: "text",
                        required:"true", //added for required form fields
                        endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        )
                      }}
                    />
                         <CustomInput
                      labelText="Last Name*"
                      id="second"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        type: "text",
                        required:"true", //added for required form fields
                        endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        )
                      }}
                    />
                    <CustomInput
                      labelText="Email*"
                      id="email"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        type: "email",
                        required:"true", //added for required form fields
                        endAdornment: (
                          <InputAdornment position="end">
                            <Email className={classes.inputIconsColor} />
                          </InputAdornment>
                        )
                      }}
                    />
                    <CustomInput
                      labelText="Phone Number*"
                      id="pass"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        type: "number",
                        required:"true", //added for required form fields
                        endAdornment: (
                          <InputAdornment position="end">
                            <Phone style={{ transform: "rotate(260deg)"}} className={classes.inputIconsColor} />
                          </InputAdornment>
                        )
                      }}
                    />
                    <CustomInput
                      labelText="Message*"
                      id="pass"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        type: "text",
                        required:"true", //added for required form fields
                        endAdornment: (
                          <InputAdornment position="end">
                            <Message className={classes.inputIconsColor} />
                          </InputAdornment>
                        )
                      }}
                    />
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                    <CustomInput
                        labelText="Submit"
                        id="pass"
                        formControlProps={{
                          fullWidth: false
                        }}
                        inputProps={{
                          type: "submit",
                        
                        }}
                      />
                  </CardFooter>
                </form>
              </Card>
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withStyles(loginStyle)(SectionLogin);
